*{
    box-sizing: border-box;
}

body{
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: #eff3f7;
    height: 100%;
} 


html,
body,
#potato,
.container{
    height:100%;
    display: flex;
    justify-content: center;
}

.loader{
    width:100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight:300;
}

.movies{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    padding: 50px;
    padding-top: 70px;
    width: 80%;
}

.movies .movie{
    width: 45%;
    background-color: white;
    display: flex;
    margin-bottom: 70px;
    align-items: flex-start;
    justify-content: space-between;
    font-weight: 300;
    padding:20px;
    border-radius:5px;
    color: #abaeb9;
    box-shadow: 0 13px 27px -5px rgba(50,50,92,0.25), 0 8px 16px -8px rgba(0,0,0,0.3), 0 -6px 16px -6px rgba(0,0,0,0.025);
}

.movie img{
    position: relative;
    top: -50px;
    max-width: 150px;
    width: 100%;
    margin-right: 30px;
    box-shadow: 0 30px 60px -12px rgba(50,50,92,0.25), 0 18px 36px -18px rgba(0,0,0,0.3), 0 -12px 36px -8px rgba(0,0,0,0.025);

}

.movie .movie_title,
.movie .movie_year{
    margin: 0;
    font-weight:300;
}

.moive .movie_title{
    margin-bottom: 5px;
    font-size: 24px;
    color: #2c2c2c;
}

.movie .movie_genres{
   list-style: none;
   padding:0;
   margin: 0;
   display: flex; 
   margin: 5px;
}

.movie_genres li{
    margin-right: 10px;
    font-size: 14px;

}
